@import "../../styles/variables.scss";

.product {
  &-slider {
    margin: 0 auto 80px;
    width: 1136px;
  }

  &-topcontainer {
    margin-bottom: 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &-title {
    font-family: $bold;
    font-size: 32px;
    line-height: $h1-line-height;
    letter-spacing: 0;
    color: $primary;
  }

  &-slide {
    height: auto;
    width: 272px !important;
  }
}

.slider {
  height: 32px;
  width: 32px;
  border: 1px solid #e2e6e9;
  transition: border-color 0.3s;

  &:hover {
    cursor: pointer;
    border-color: #b4bdc3;
  }

  &-arrows {
    margin-right: 7px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;
  }

  &-icon {
    height: 32px;
    width: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $icons;
    transition: border-color 0.3s;

    &:hover {
      cursor: pointer;
    }
  }

  &-container {
    max-width: 1136px;
  }
}

.wrapper {
  padding: 0 5px !important;
}
