@import "../../styles/variables.scss";

.favourites {
  margin: 20px auto 80px;
  width: 1136px;
  min-height: calc(100vh - 178px);

  &__nav {
    margin-bottom: 40px;
    width: fit-content;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }

  &__navtext {
    color: $secondary;
    font-family: $semi-bold;
    font-size: $small-text;
  }

  &__title {
    margin-bottom: 8px;
    color: $primary;
    font-size: $h1;
    font-family: $bold;
  }

  &__subtitle {
    margin-bottom: 40px;
    color: $secondary;
    font-size: $body-text;
    font-family: $regular;
  }

  &__products {
    margin-bottom: 40px;
    display: flex;
    flex-wrap: wrap;
    column-gap: 16px;
    row-gap: 40px;
  }
}

.nofavourites {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  &__text {
    color: $primary;
    margin-bottom: 30px;
    font-family: $regular;
    font-weight: lighter;
    font-size: $h2;
    line-height: $body-line-height;
    text-align: center;
  }

  &__button {
    width: 263px;
    height: 48px;
    background-color: $white;
    color: $primary;
    border: 1px solid $primary;
    text-align: center;
    font-family: $semi-bold;
    font-size: $body-text;
    line-height: $body-line-height;
  }

  &__button:hover {
    background-color: $primary;
    color: $white;
  }
}

.navicon {
  width: 16;
  height: 16;
  align-items: center;
}
